<template>
    <v-container>
        <v-toolbar flat color="white">
            <v-toolbar-title>Crud Atma Pedia</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
                <v-text-field
                v-model="search"
                append-icon="mdi-search-web"
                label="Search"
                single-line
                hide-details
            ></v-text-field>
            <v-spacer></v-spacer>

            <v-divider class="mx-4" inset vertical></v-divider>

            <v-dialog v-model="dialog" max-width="500px">
                <template v-slot:activator="{ on }">
                    <v-btn color="primary" dark class="mb-2" v-on="on">Buat Makanan</v-btn>
                </template>
                <v-card>
                    <v-card-title>
                        <span class="headline">{{ formTitle }}</span>
                    </v-card-title>
                
                    <v-card-text>
                        <v-container>
                            <v-row>
                                <v-col cols="12" sm="6" md="4">
                                    <v-text-field v-model="editedItem.name" label="Dessert name"></v-text-field>
                                </v-col>
                                <v-col cols="12" sm="6" md="4">
                                    <v-text-field v-model="editedItem.calories" label="Calories"></v-text-field>
                                </v-col>
                                <v-col cols="12" sm="6" md="4">
                                    <v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
                                </v-col>
                                <v-col cols="12" sm="6" md="4">
                                    <v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
                                </v-col>
                                <v-col cols="12" sm="6" md="4">
                                    <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
                                </v-col>
                            </v-row>
                        </v-container>
                    </v-card-text>

                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                        <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
        </v-toolbar>

        <v-row>
            <v-col cols="3" v-for="dessert in desserts" :key="dessert.name">
                <v-card max-width="344" raised class="mx-auto">
                    <v-list-item three-line>
                        <v-list-item-content>
                            <div class="overline mb-4">MAKANAN</div>

                            <v-list-item-title class="display-1 text--primary">{{ dessert.name }}</v-list-item-title>
                            <v-list-item-subtitle>Calories : {{ dessert.calories }}</v-list-item-subtitle>
                            <v-list-item-subtitle>Fat(g) : {{ dessert.fat }}</v-list-item-subtitle>
                            <v-list-item-subtitle>Carbs(g) : {{ dessert.carbs }}</v-list-item-subtitle>
                            <v-list-item-subtitle>Protein(g): {{ dessert.protein }}</v-list-item-subtitle>

                        </v-list-item-content>    
                    </v-list-item>   
                    <v-spacer></v-spacer>
                    <v-card-actions>
                        <v-btn @click="editItem(dessert)" color="green darken-2">EDIT</v-btn>
                        <v-btn @click="deleteItem(dessert)" color="red">DELETE</v-btn>
                    </v-card-actions> 
                </v-card>
                
            </v-col>    
        </v-row>
    </v-container>   
</template>

<script>

import{ dessertsRef } from '../firebase';

    export default {
        data: () => ({
            cek : -1,
            search:'',
            dialog: false,
            selected: [],
            desserts: [],
            editedIndex: -1,
            editedItem: {
                name: '',
                calories: 0,
                fat: 0,
                carbs: 0,
                protein: 0,
            },
            defaultItem: {
                name: '',
                calories: 0,
                fat: 0,
                carbs: 0,
                protein: 0,
            },
        }),
        firebase: {
            desserts : dessertsRef
        },
        computed: {
            formTitle() {
                return this.cek === -1 ? 'New Item' : 'Edit Item'
            },
        },
        watch: {
            dialog(val) {
                val || this.close()
            },
        },

        methods: {
            close() {
                this.dialog = false
            },
            // methods untuk save
            save() {
                if (this.cek > -1) {
                    dessertsRef.child(this.editedIndex).set({
                        name : this.editedItem.name,
                        calories : this.editedItem.calories, 
                        fat : this.editedItem.fat,
                        carbs: this.editedItem.carbs,
                        protein: this.editedItem.protein})
                        this.clear()
                } else {
                    dessertsRef.push({
                        name: this.editedItem.name,
                        calories: this.editedItem.calories,
                        fat: this.editedItem.fat,
                        carbs: this.editedItem.carbs,
                        protein: this.editedItem.protein})
                        this.clear()
                }
                this.close()
            },
            editItem (item) {
                this.editedIndex = item['.key']
                this.cek = 0
                this.editedItem = Object.assign({}, item)
                this.dialog = true
            },
            deleteItem(item) {
                confirm('Are you sure you want to delete this item?') && dessertsRef.child((item['.key'])).remove()
            },
            clear(){
                this.editedItem={}
                this.editedItem.name= ''
                this.editedItem.calories= 0
                this.editedItem.fat= 0
                this.editedItem.carbs= 0
                this.editedItem.protein= 0
                this.cek=-1
            },
        },
    }
</script>